import React, { useState } from 'react';

import { Link } from 'react-router-dom';

import { Row, Col } from 'react-bootstrap';

import Faqs from './Faqs';
import Reviews from './Reviews';
import Cancelation from './Cancelation';

import BundleWrapper from './styled/';

import { BsStarFill, BsCheck } from 'react-icons/bs';

import { RiShoppingCart2Line } from 'react-icons/ri';

import { BiShareAlt } from 'react-icons/bi';

import Flatpickr from "react-flatpickr";

import { RiArrowRightSLine } from 'react-icons/ri';

const Overview = () => {  
    const [date, setDate] = useState(new Date());
    
    const [form, setForm] = useState();


    const handleForm = () => {
        setForm(!form);
    }

    return (
        <>
           
            <BundleWrapper className="bg-light pb-5">
            
                    
                    <div className="row pt-4">
                        <div className="col-md-8 bg-white pad-20">

                            <div className="pt-3 overview">
                                <h3>Overview</h3>
                            </div>

                            <div className="row mt-4">
                                <div className="col-md-4">
                                    <div className="bundle-img-step">
                                        <img alt="icon_map" className="icon_map" src="../../images/bundles/bundle.png" />
                                    </div>
                                </div>
                                <div className="col-md-1">
                                    <Row className="mt-3">
                                        <Col xs={2}>
                                            <div className="up-div">

                                                <div className="mid1-div">
                                                    1
                                                </div>



                                                <div className="mid2-div">
                                                    2
                                                </div>


                                            </div>
                                        </Col>
                                    

                                    </Row>
                                </div> 
                                <div className="col-md-7">
                                    <h3>Amsterdam North Canal Cruise</h3>
                                    <p className="bundle-info">
                                        Amsterdam North (?Noord? in Dutch) is the new hotspot of Amsterdam. 
                                        The spot has a more modern feel to it, but is still capturing the atmosphere of the city center. 
                                        Locals have discovered it?s creative energy, but it?s also becoming more and more popular among people visiting Amsterdam.
                                    </p>
                            
                                    <Flatpickr
                                        onChange={date => {
                                            setDate({date: date});
                                        }}
                                        placeholder="DD/MM/YY"
                                        options={
                                            {
                                                minDate: new Date(),
                                                dateFormat: "d-m-Y"
                                            }
                                        }      
                                    />

                                    <div className="check"
                                    onClick={handleForm}
                                    >
                                        <RiArrowRightSLine  style={{ height: '30px', width: '30px', color: '#fff', marginTop: '-6px' }} /> 
                                        <RiArrowRightSLine  style={{ height: '30px', width: '30px', color: '#fff', marginLeft: '-20px', marginTop: '-5px' }} /> 
                                        Check availabilty
                                    </div>

                                </div>
                            </div>

                            <div className="row mt-5">
                                <div className="col-md-4">
                                    <div className="bundle-img-step">
                                        <img alt="icon_map" className="icon_map" src="../../images/bundles/bundle.png" />
                                    </div>
                                </div>
                                <div className="col-md-1">
                                 
                                </div> 
                                <div className="col-md-7">
                                    <h3>Private Zaan windmill 3-course dinner cruise from Amsterdam</h3>
                                    <p className="bundle-info">
                                        Embark on a unique culinary and sightseeing 
                                        experience with a private dinner cruise along the Zaan River. Glide past windmills while a knowledgeable 
                                        skipper and guide shares stories of life on the river and among the surrounding landscape.
                                    </p>
                            
                                    <Flatpickr
                                        onChange={date => {
                                            setDate({date: date});
                                        }}
                                        placeholder="DD/MM/YY"
                                        options={
                                            {
                                                minDate: new Date(),
                                                dateFormat: "d-m-Y"
                                            }
                                        }
                                        
                                    />

                                    <div 
                                        className="check"
                                        onClick={handleForm}
                                    >
                                        <RiArrowRightSLine  style={{ height: '30px', width: '30px', color: '#fff', marginTop: '-6px' }} /> 
                                        <RiArrowRightSLine  style={{ height: '30px', width: '30px', color: '#fff', marginLeft: '-20px', marginTop: '-5px' }} /> 
                                        Check availabilty
                                    </div>

                                </div>
                            </div>

                            {
                                form && (

                                    <div className="form mt-5">
                                        <div className="row">
                                            <div className="col-md-6">
                                                <label> Time: </label>
                                                <input type="text" className="inputform" />
                                            </div>
                                            <div className="col-md-6">
                                                <label> Passengers: </label>
                                                <input type="text" className="inputform" />
                                            </div>
                                        </div>
                                        <div className="row mt-4">
                                            <div className="col-md-6">
                                                <label> Email Address: </label>
                                                <input type="text" className="inputform" />
                                            </div>
                                            <div className="col-md-6">
                                                <label> Phone Number: </label>
                                                <input type="text" className="inputform" />
                                            </div>
                                        </div>
                                        <div className="row mt-4">
                                            <div className="col-md-12">
                                                <label> Passenger Names: </label>
                                                <textarea />
                                            </div>
                                        </div>
                                        <div className="row mt-4 extras">
                                            <div className="col-md-12">
                                                <p>Add Extra</p>
                                            </div>
                                            <div className="col-md-12">
                                            <Row>
                                                 <div className="col-md-12"><span><input type="checkbox"/> Service per booking</span>  <span className="float-right">79.10</span></div>
                                             
                                                
                                            </Row>
                                           <hr/>
                                            </div>
                                            <div className="col-md-12">
                                            <Row>
                                                 <div className="col-md-6"><input type="checkbox"/> Service per booking</div>
                                            </Row>
                                            </div> 
                                            <div className="col-md-4 mt-2">
                                            <span>Adult: 55.00</span>
                                                 <span className="float-right">Youth: 30.00</span>
                                            
                                            </div> 
                                            <br/>  
                                            <div className="col-md-12 mt-2">
                                            <Row>
                                            <div className="col-md-12"><span>Confirm Now</span></div>
                                                 <div className="col-md-2"><span>Yes <input type="radio"/></span><span className="float-right">No <input type="radio"/></span></div>
                                               
                                            </Row>
                                            </div> 
                                        </div>

                                    </div>

                                )
                            }


                            <div className="row mt-5">

                           <div className="col">
                            <hr/> <br/>

                              <h3>FAQs</h3>
                                 <Faqs/>

                                 </div>
                            </div>

                            <div className="row mt-5">
                            <div className="col">
                            <hr/> <br/>

                                <h3>Reviews</h3>
                                <br/>
                                <Reviews/>
                                </div>
                            </div>

                            <div className="row mt-5">
                            <div className="col">
                            <hr/> <br/>
                                <h3>Cancelation Policy</h3>

                                <Cancelation/>
                                </div>
                            </div>

                        </div>

                        <div className="col-md-4 book-tab">
                            <div className="bg-white pad-20">
                                
                                <div className="right">
                                    <h3>
                                        129.10
                                    </h3>
                                    <p>per person</p>
                                </div>
                                <div className="review-wrapper">
                                    <BsStarFill className="star-full" />
                                    <BsStarFill className="star-full" />
                                    <BsStarFill className="star-full" />
                                    <BsStarFill className="star-full" />
                                    <BsStarFill className="star-empty" />
                                    <span className="review">25 Reviews</span>
                                </div>
                                <Link to="payment">
                                    <button className="btn-book mt-4">
                                        <RiShoppingCart2Line className="book-icon" /> Book now
                                    </button>
                                </Link>
                                <div className="share-wrapper">
                                    <button className="btn-share mt-1">
                                        <BiShareAlt className="share-icon" /> Share
                                    </button>
                                </div>
                                <br/>
                                <div >
                                    <h3>
                                        Featured benefits
                                    </h3>
                                    <p className="mb-0"><span className="mr-2"><BsCheck/></span>Free cancellation</p>
                                    <p className="mb-0"><span className="mr-2"><BsCheck/></span>Mobile ticketing</p>
                                    <p className="mb-0"><span className="mr-2"><BsCheck/></span>Wheelchair accessible</p>
                                    <p className="mb-0"><span className="mr-2"><BsCheck/></span>Instant confirmation</p>
                                </div>

                            </div>
                        </div>
                    </div> 
               
            </BundleWrapper>        
        </>
    )
} 

export default Overview

