import React, { useState } from 'react';

import { Link } from 'react-router-dom';
import BundleWrapper from './styled/';
import Banner from './styled/banner';

import Overview from './Overview';
import Faqs from './Faqs';
import Reviews from './Reviews';
import Cancelation from './Cancelation';

import { Row, Col, Tab, Nav } from 'react-bootstrap';



const Bundle = () => {  
    const [date, setDate] = useState(new Date());
    
    const [form, setForm] = useState();


    const handleForm = () => {
        setForm(!form);
    }

    return (
        <>
            <Banner>
                <div className="container">
                    <h1 className="padTop-50">Luxury tour</h1>   
                </div>  
            </Banner>

           

            <BundleWrapper className="bg-light pb-5">
            <Tab.Container id="left-tabs-example" defaultActiveKey="first">
 
 
                    <div className="container">
                      
    <Col xs={12} className="upbar">
      <Nav variant="pills" >
        <Nav.Item>
          <Nav.Link eventKey="first">Overview</Nav.Link>
        </Nav.Item>
        <Nav.Item className="pl-3">
          <Nav.Link eventKey="second" >FAQS</Nav.Link>
        </Nav.Item>
        <Nav.Item className="pl-3">
          <Nav.Link eventKey="third" >Reviews</Nav.Link>
        </Nav.Item>
        <Nav.Item className="pl-3">
          <Nav.Link eventKey="fourth" >Cancellation policy</Nav.Link>
        </Nav.Item>
      </Nav>
    </Col>
    <Col xs={12}>
      <Tab.Content>
        <Tab.Pane eventKey="first">
         <Overview/>
        </Tab.Pane>
        <Tab.Pane eventKey="second">
          <Faqs/>
        </Tab.Pane>
        <Tab.Pane eventKey="third">
          <Reviews/>
        </Tab.Pane>
        <Tab.Pane eventKey="fourth">
          <Cancelation/>
        </Tab.Pane>
      </Tab.Content>
    </Col>
  
  </div>
</Tab.Container>


            </BundleWrapper>



              
        </>
    )
} 

export default Bundle

