import styled from 'styled-components';

const component = styled.div.attrs({ className: 'buttons' })`
    
    *{
       font-family:poppins;
    }

    .buttons {
        background: #EBEBEB;
        text-align: center;
        font-size: 20px;
        font-style: italic;
      
        font-weight: 501;
    }

    .button {
        text-algn: center;

        &:hover {
            cursor: pointer;
        }
    }

    #active {
        border: 2px solid #485460;
        border-radius: 5px;
        padding-top: 5px;
        padding-bottom: 5px;
        background:white;
        color: #485460;
        box-shadow: 0px 0px 3px 2px lightgray;
    }

    #inActive {
        border: 3px solid #EBEBEB;
        border-radius: 5px;
        padding-top: 5px;
        padding-bottom: 5px;
        color: #485460;
    }
    .smf{
        font-size: 12px !important;
    }
 
`;

export default component;
