import React from 'react';

import Nav from '../Header/Nav/Nav';

import Search from '../Header/Search/Search';

import Banner from './styled/banner';

import Bundles from './styled';

import Searchmob from '../Header/Searchmob/Searchmob';

import Footer from '../Footer';

import Featured from './styled/featured';

const Destinations = props => {    
    return (
        <>
            <Nav />
            <Search />
             <Banner>

                <div className="container">
                    <h1 className="padTop-50">Destinations</h1>
                </div>
            </Banner>
            <Bundles>
            <Searchmob/>
                 <div className="pt-5 pb-5">
                    <div className="container">
                        <div className="col-xs-12">
                            <h1 className="mt-3 mb-4">About London</h1>
                            <p >
                              
Amsterdam North (?Noord? in Dutch) is the new hotspot 
of Amsterdam.  The spot has a more modern feel to it, but 
is still capturing the atmosphere of the city center. Locals 
have discovered it?s creative energy, but it?s also 
becoming more and more popular among people 
visiting Amsterdam.                            </p>
                           
                        </div>
                        
                    </div>
                </div>
            </Bundles>

                 <Featured>
                <div className="container pt-5">
                    <h1>Featured trips</h1>
                    <br />
                    <div className="row">

                        <div className="col-md-6">
                            <div className="featured-trips" 
                                style={{ 
                                backgroundImage: "url(../../images/amsterdam.jpg)",
                                backgroundSize: "cover",
                                height:"400px",
                                borderRadius: "10px"
                            }}>
                                <div className="featured-trips-info">
                                    <h4>Destination/Experience</h4>
                                    <h5>From 29</h5> 
                                </div>
                            </div>
                        </div>

                        <div className="col-md-6">
                            <div className="featured-trips" 
                                style={{ 
                                backgroundImage: "url(../../images/barcelona.jpg)",
                                backgroundSize: "cover",
                                height:"400px",
                                borderRadius: "10px"
                            }}>
                                <div className="featured-trips-info">
                                    <h4>Destination/Experience</h4>
                                    <h5>From 29</h5> 
                                </div>
                            </div>
                        </div>

                    </div>
                </div> 

                <br /> 

                <div className="container pt-5 pt-5" style={{ paddingBottom: "40px"}}>
                    <h1>Top destinations</h1>
                    <br />
                    <div className="row">

                        <div className="col-sm">
                            <div className="featured-trips" 
                                style={{ 
                                backgroundImage: "url(../../images/amsterdam.jpg)",
                                backgroundSize: "cover",
                                height:"400px",
                                borderRadius: "10px"
                            }}>
                                <div className="featured-trips-info">
                                    <h4>Amsterdam</h4>
                                    <h5>From 29</h5> 
                                </div>
                            </div>
                        </div>

                        <div className="col-sm">
                            <div className="featured-trips" 
                                style={{ 
                                backgroundImage: "url(../../images/athens.jpg)",
                                backgroundSize: "cover",
                                height:"400px",
                                borderRadius: "10px"
                            }}>
                                <div className="featured-trips-info">
                                    <h4>Athens</h4>
                                    <h5>From 29</h5> 
                                </div>
                            </div>
                        </div>

                        <div className="col-sm">
                            <div className="featured-trips" 
                                style={{ 
                                backgroundImage: "url(../../images/barcelona.jpg)",
                                backgroundSize: "cover",
                                height:"400px",
                                borderRadius: "10px"
                            }}>
                                <div className="featured-trips-info">
                                    <h4>Barcelona</h4>
                                    <h5>From 29</h5> 
                                </div>
                            </div>
                        </div>

                        <div className="col-sm">
                            <div className="featured-trips" 
                                style={{ 
                                backgroundImage: "url(../../images/rome.jpg)",
                                backgroundSize: "cover",
                                height:"400px",
                                borderRadius: "10px"
                            }}>
                                <div className="featured-trips-info">
                                    <h4>Florence</h4>
                                    <h5>From 29</h5> 
                                </div>
                            </div>
                        </div>

                        <div className="col-sm">
                            <div className="featured-trips" 
                                style={{ 
                                backgroundImage: "url(../../images/newyork.jpg)",
                                backgroundSize: "cover",
                                height:"400px",
                                borderRadius: "10px"
                            }}>
                                <div className="featured-trips-info">
                                    <h4>New York</h4>
                                    <h5>From 29</h5> 
                                </div>
                            </div>
                        </div>
                    </div>
                </div> 
            </Featured>


            <Footer />      
        </>
    ) 
} 

export default Destinations


