import React, { useState } from "react";

import { RiShoppingCart2Line, RiUserLine } from 'react-icons/ri';

import { Link, NavLink } from 'react-router-dom';

import NavLinks from './styled';


const Nav = props => {

    const [show, setShow] = useState(false);

    return (

        <>
            <NavLinks>
                <section className="navbar-bg"  >
                    <nav className="navbar navbar-expand-lg navbar-light ">
                        <div className="container">

                            <button
                                className="navbar-toggler login-btn"
                                type="button"
                                data-bs-toggle="collapse"
                                data-bs-target="#navbarSupportedContent"
                                aria-controls="navbarSupportedContent"
                                aria-expanded="false"
                                aria-label="Toggle navigation"
                                onClick={() => setShow(!show)}>
                                <span className="navbar-toggler-icon"></span>
                            </button>

                           
                                <div className="col-xs-4 text-left">
                                    <Link to="/">
                                        <img alt="logo" className="logo" src="../../images/tixxly_logo.png" />
                                    </Link>
                                </div>
                                <span className="logo-txt">curated travel<br/>experiences</span>
                                <div className="col-xs-4 text-left icon-nav" >
                                    <Link to="/">
                                         <RiUserLine style={{ color: '#c7cbcf', width: '24px', height: '24px' }} />
                                    </Link>
    
                                    <Link to="/">
                                         <RiShoppingCart2Line style={{ color: '#c7cbcf', width: '24px', height: '24px', marginLeft: '10px' }} />
                                    </Link>
                                </div>

                            <div className={`collapse navbar-collapse ${show ? "show" : ""}`}>
                                <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                                    
                                    <li className="nav-item">
                                        <NavLink className="nav-link" to="/experiences">
                                            Experiences
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link" to="/bundles">
                                            Destinations
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link active" aria-current="page" to="our-story">
                                            Our Story
                                        </NavLink>
                                    </li>
                                    

                                    <li>
                                        <button className="nav-link-btn">
                                            Sign in / Sign up
                                        </button>

                                    </li>
                                    <li>
                                        <button className="nav-link-crtbtn">


                                            <RiShoppingCart2Line style={{ color: 'white', width: '30px', height: '35px', marginLeft: '10px' }} />

                                        </button>
                                    </li>
                                </ul>
                            </div>


                        </div>
                    </nav>
                </section>
            </NavLinks>
        </>
    );
}

export default Nav
