import styled from 'styled-components';

const component = styled.div.attrs({ className: 'nav-link' })`
    background: #485460;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
    
   img{
       height:33px;
   }
   a {
        color: #fff !important;
    }

    .nav-link-btn {
        background: #FC5A5A;
        border-radius: 5px;
        color: #fff;
        padding: 10px 20px;
        font-weight: bold;
        border: 1px solid #FC5A5A; 
    }
    .nav-link-crtbtn{
        background:none;
        border:none;
    }
    .logo-txt{
        display: inline-block;
        font-size: 12px;
        padding: 6px;
        color: #c7cbcf;
        font-family: 'Poppins', sans-serif;
    }
    .icon-nav{
        display:none;
    }
    ul {
        list-style-type: none;
        color: white;
        margin-top: 10px;

        li {
            color: #fff;
            float: right;
            margin-left: 14px ;
            text-decoration: none;
            font-size: 14px;  
        }

        :hover {
            cursor: pointer;
        }

    }
    @media only screen and (max-width: 810px) {

    .nav-link-crtbtn , .nav-link-btn, .logo-txt {
        display: none;

    }

    li {
            
            margin-left: 0px !important;

          
        }
        img{
       height:24px;
   }
   .icon-nav{
        display:inline;
    }
    .navbar-toggler{
        border:none;
    }
    .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(251,251,251, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
  
}
   

    }
`;

export default component;