import styled from 'styled-components';

const component = styled.div.attrs({ className: 'bundles' })`
    @media only screen and (max-width: 810px) {
        .mobsrc {
    background-color:white !important;
    
    
        }
        .mob-crc{
            margin-top:10%;
        }
        .box-srch{
               margin-top: 0px;
               margin-top: -50px;
        }
        .bundl-mob{
            text-align: center !important;
        }

        .box-srch2{
               
               margin-top: -50px;
        }
      
    }   

    h1 {
        font-size: 30px;
        color: #3B4654;
    }


    .imgCircle {
       border: 2px solid rgba(74, 84, 99, 0.14);
border-radius: 50%;
height: 200px;
width: 200px;
position: relative;
margin: 0 auto;
box-shadow: 0px 0px 6px 17px #d3d3d34a;

        img {
            position: absolute;
            right: 0;
            left: 0;
            top: 0;
            bottom: 0;
            margin: auto auto;
        }

    }

    .featured-trips-info {
        color: #fff;
        position: absolute;
        bottom: 0;
        padding: 20px;
    }



`;

export default component;
